/*eslint-disable*/module.exports={messages:{"### Entsch√§digungen nach der EU-Fluggastrechteverordnung\n- Sie haben wegen einer Flugversp√§tung, einer Annullierung durch die Fluglinie oder einer Bef√∂rderungsverweigerung wegen √úberbuchung Ihr Ziel versp√§tet oder gar nicht erreicht? Dann haben Sie nach der EU-Fluggastrechteverordnung vielleicht einen Anspruch auf eine Ausgleichszahlung. \n- Die H√∂he der  Ausgleichszahlung ist abh√§ngig von der Flugstrecke und kann bis zu 600 Euro betragen. \n- Bei Annullierung haben Sie zudem einen Anspruch auf Erstattung der Flugscheinkosten oder eine alternative Bef√∂rderung.\n- F√ºr lange Wartezeiten am Flughafen k√∂nnen Sie auch Betreuungsleistungen wie Essen und Getr√§nke, oder eine n√§chtliche Hotel√ºbernachtung erhalten.":"### Compensation under the EU Passenger Rights Regulation\n- Have you reached your destination late or not at all due to a flight delay, airline cancellation or refusal of carriage due to overbooking? Then you may be entitled to compensation under the EU Passenger Rights Regulation. \n- The amount of the compensation payment depends on the route of the flight and can be up to 600 euros. \n- In the event of cancellation, you are also entitled to a refund of ticket costs or alternative transportation.\n- For long waiting times at the airport, you can also receive assistance such as food and drinks, or an overnight stay in a hotel at night.","### Was ist ein **Mietmangel**?\nIhre Vermieter:in muss Ihnen Ihre Wohnung in einem geeigneten Zustand √ºberlassen und erhalten. Ist die **Wohnung in einem erheblich schlechteren Zustand** als erwartet werden kann, liegt vielleicht ein Mangel vor. Dann k√∂nnen Sie verschiedene Anspr√ºche haben, beispielsweise auf Beseitigung des Mangels oder Mietminderung.":"### What is a **rent deficiency**?\nYour landlord:in must leave and maintain your apartment in a suitable condition. If the **apartment is in a significantly worse condition** than can be expected, there may be a defect. Then you can have various claims, for example to remedy the defect or to reduce rent.","### Wohnungsm√§ngel durch L√§rm\nErheblicher L√§rm kann als Mietmangel gelten, beispielswese bei St√∂rungen in den Ruhezeiten. Diese sind in der Regel zwischen 22 und 6 Uhr, sowie ganzt√§gig an Sonn- und Feiertagen. Andere Ruhezeiten k√∂nnen von Bundesl√§ndern und Gemeinden, aber auch in der Hausordnung festgehalten werden.\n‚úÖ **Unter Ruhest√∂rungen fallen**¬†St√∂rungen der Nachtruhe (Musik, Staubsaugen, laute Waschmaschine √ºber 30 Dezibel), dauerhafte Ger√§usche von Tieren, laute Gartenger√§te, sowie Streitgespr√§che.\n‚ùå **Nicht als Ruhest√∂rung z√§hlen** n√§chtliches Duschen, Renovierungs- und Baul√§rm, spielende Kinder, oder Kirchenglocken.\nüí° **Hinweis bei Nachbarschaftskonflikten:** Sollten Sie sich von einer Nachbar:in gest√∂rt f√ºhlen, sollten Sie zun√§chst freunddlich darauf ansprechen, um Streit zu vermeiden. M√∂glicherweise ist der Nachbar:in nicht klar, dass Sie sich gest√∂rt f√ºhlen. Bei akuten, sehr starken St√∂rungen k√∂nnen Sie auch das Ordnungsamt oder die Polizei kontaktieren.":"### Housing deficiencies due to noise\nSignificant noise can be considered a lack of rent, for example in case of disruptions during rest periods. These are usually between 10pm and 6am, as well as all day on Sundays and public holidays. Other rest periods can be recorded by federal states and municipalities, as well as in the house rules.\n‚úÖ **Disturbed peace** Disturbances of sleep (music, vacuuming, noisy washing machine over 30 decibels), persistent sounds from animals, loud garden tools, and arguments.\n‚ùå **Don't count as a disturbance of peace** Nightly showering, renovation and construction noise, children playing, or church bells.\nüí° **Neighborhood conflict note**: If you feel disturbed by a neighbor, you should first approach it kindly to avoid quarrels. The neighbor may not realise that you are feeling disturbed. In case of acute, very severe disturbances, you can also contact the public order office or the police.","### Wohnungsm√§ngel durch Schimmel\nSchimmelpilze sehen aus wie schwarze Flecken und entstehen bei zu hoher Feuchtigkeit in der Wohnung, beispielsweise durch seltenes L√ºften, sparsames Heizen oder Baum√§ngel. Schimmel kann sich sowohl negativ auf die Gesundheit, als auch die W√§nde der Wohnung auswirken. [Mehr k√∂nnen Sie auf der Webseite der Verbraucherzentrale erfahren.](https://www.verbraucherzentrale.de/wissen/umwelt-haushalt/wohnen/schimmel-in-der-wohnung-6794#)":"### Housing defects due to mold\nMolds look like black spots and are formed when humidity is too high in the home, for example due to infrequent ventilation, economical heating or construction defects. Mold can have a negative impact on both health and the walls of the apartment. [You can find out more on the website of the consumer center.](https://www.verbraucherzentrale.de/wissen/umwelt-haushalt/wohnen/schimmel-in-der-wohnung-6794#)","### Wohnungsm√§ngel durch Wassersch√§den\nWassersch√§den in der Wohnung k√∂nnen beispielsweise durch starken Regen oder Sch√§den an Wasserleitungen, Ger√§ten und im Bad entstehen.\nAnspr√ºche gegen Vermieter:innen oder Hauseigent√ºmer:innen wegen des Wasserschadens bestehen dann, wenn diese den Mangel verschuldet haben, was beispielsweise oft bei Rohrbr√ºchen der Fall ist.\nWerden Wassersch√§den durch eigene defekte Ger√§te wie Waschmaschine oder Geschirrsp√ºler verursacht, haftet man in der Regel selbst. Etwas anderes kann gelten, wenn die Ger√§te mit der Wohnung angemietet wurden (bspw. Ger√§te in einer angemieteten Einbauk√ºche).\nWenn die Mietenden den Wasserschaden selbst verursacht haben, dann kann eine Hausratsversicherung oder bei Sch√§den der Nachbarn eine Haftpflichtversicherung weiterhelfen.":"### Housing deficiencies due to water damage\nWater damage in the home can be caused, for example, by heavy rain or damage to water pipes, appliances and in the bathroom.\nThere are claims against landlords or homeowners for water damage when they have caused the defect, which is often the case with pipe bursts, for example.\nIf water damage is caused by defective appliances such as washing machines or dishwashers, you are usually liable yourself. Something different may apply if the equipment was rented with the apartment (e.g. appliances in a rented fitted kitchen).\nIf the tenants have caused the water damage themselves, household insurance or liability insurance can help in case of damage to neighbours.","**Wie funktioniert eine Klage?**\n- Wenn Sie einen Konflikt mit einer Person oder einem Unternehmen nicht allein kl√§ren k√∂nnen, kann eine Richter:in diesen in einem Gerichtsprozess f√ºr Sie l√∂sen.\n- Mit einer Klage beginnen Sie einen Gerichtsprozess.\n- Im Prozess stellen Sie und die andere Partei den Fall vor der Richter:in dar. Wenn nicht klar ist, welche Version stimmt, werden Beweise erhoben.\n- Zun√§chst wird die Richter:in Ihnen und der Gegenpartei eine Einigungsm√∂glichkeit als Vergleich vorschlagen, um den Streit schnell beizulegen.\n- Wird keine Einigung gefunden, verk√ºndet die Richter:in am Ende des Prozesses ein verbindliches Urteil verk√ºndet. Darin steht, welche Anspr√ºche Ihnen zustehen.\n- Insgesamt dauert ein Prozess √ºblicherweise zwischen drei und zehn Monaten.\n###\n**Was kostet ein Gerichtsprozess?**\n- Die Verlierer:in des Prozesses tr√§gt die Kosten. Wenn keine Partei ganz Recht bekommt, werden die Kosten aufgeteilt.\n- Die H√∂he der Kosten ist abh√§ngig vom Wert der verhandelten Forderung.\n- Sie k√∂nnen [Prozesskostenhilfe](https://www.bmjv.de/DE/Themen/GerichtsverfahrenUndStreitschlichtung/Prozesskostenhilfe/Prozesskostenhilfe_node.html) beantragen, falls Sie wenig Geld haben.\n###\n**Wer kann klagen?**\n- Jede Person ab 18 Jahren kann alleine am Amtsgericht klagen.\n- Eltern k√∂nnen f√ºr minderj√§hrigen Kinder klagen, zum Beispiel bei Anspr√ºchen aus der EU-Fluggastrechteverordnung.\n- Wenn Sie sich dabei unsicher f√ºhlen, k√∂nnen Anw√§lt:innen Sie beraten oder ganz vertreten.":"**How does a lawsuit work?**\n- If you cannot resolve a dispute with a person or a company on your own, a judge can resolve it for you in a court case.\n- With a lawsuit, you start a court case.\n- In the trial, you and the other party present the case before the judge. If it is not clear which version is correct, evidence will be collected.\n- First, the judge will propose a settlement option to you and the other party in order to resolve the dispute quickly.\n- If no agreement is found, the judge will announce a binding verdict at the end of the trial. It says what demands you are entitled to.\n- In total, a process usually takes between three and ten months.\n###\n**What does a court case cost?**\n- The losers in the process are ordered to pay the costs. If neither party is quite right, the costs are split up.\n- The amount of the costs depends on the value of the negotiated claim.\n- You can apply for [legal aid](https://www.bmjv.de/DE/Themen/GerichtsverfahrenUndStreitschlichtung/Prozesskostenhilfe/Prozesskostenhilfe_node.html) if you are short on money.\n###\n**Who can sue?**\n- Any person aged 18 and over can sue in the district court alone.\n- Parents can sue for underage children, for example in the case of claims from the EU Passenger Rights Regulation.\n- If you feel unsure about it, lawyers can advise you or represent you entirely.","- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie d√ºrfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels im Verzug** ist oder es sich um eine **Not- oder Eilma√ünahme** handelt.\n- Meistens sollten Sie den Mangel erst dann selbstst√§ndig beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben und der Mangel danach nicht behoben wurde.":"- A claim to **reimbursement of expense** is the right to **reimbursement of the costs** incurred in the **elimination of the defect**.\n- You may rectify the defect yourself if the lessor is in arrears with the elimination of the defect or it is a **emergency or urgent measure**.\n- In most cases, you should not repair the defect yourself until you have asked your landlady to remedy it with a deadline and the defect has not been rectified afterwards.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entsch√§digung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Versp√§tung**, **Annullierung** oder **√úberbuchung** nicht oder nur versp√§tet erreichen konnten.\n- Die **H√∂he der Zahlung** richtet sich nach der **Flugstrecke** und betr√§gt **250‚Ç¨ (unter 1.500km)**, **400‚Ç¨ (zwischen 1.500km und 3.500km)** oder **600‚Ç¨ (√ºber 3.500km)**.":"- A claim to **Compensation** is the right to a **Compensation** from your airline.\n- You will receive a chargeback if you were unable to reach your destination or were delayed due to **delay**, **cancellation**, or **overbooking**.\n- The **amount of payment** depends on the **flight route** and is **‚Ç¨250 (under 1,500 km)**, **‚Ç¨400 (between 1,500 km and 3,500 km)** or **‚Ç¨600 (over 3,500 km)**.","- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zus√§tzliche Unterst√ºtzung wie Essen und Getr√§nke, wenn sich Ihr **Abflug stark verz√∂gert**.\n- Betreuungsleistungen m√ºssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getr√§nke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, k√∂nnen Sie **Schadensersatz** f√ºr notwendige Ausgaben mit Rechnungen beantragen.":"- A claim to **care services** is the right to additional support such as food and drinks if your **departure is severely delayed**.\n- Care services must be **appropriate to the waiting time and time of day**. (Small) meals and drinks, and access to telephone or internet are always appropriate.\n- If there are **long waiting times at the airport at night**, you may also have the right to a **hotel accommodation** with transport there. \n- As a rule, these services are offered to you **by the airline**; if not, you can claim **compensation** for necessary expenses with invoices.","- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschr√§nkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch ist und die Wohnqualit√§t stark beeintr√§chtigt wird.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine K√ºndigung wegen Mietverzug riskieren. Stattdessen k√∂nnen Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels k√∂nnen Sie dann einen Anteil zur√ºckfordern.\n- Um **wie viel Prozent** die Gesamtmiete gek√ºrzt werden kann, h√§ngt von der **Schwere des Mangels** ab. Einen Anhaltspunkt f√ºr Ihren Fall k√∂nnen Sie in einer **Mietminderungstabelle** im Internet finden.":"- A claim to **rent reduction** is the right to pay a **lower total rent** if the **use of the apartment**\nis **significantly limited** due to a defect. \n- There is a **significant deficiency** when the defect is not only visual and the quality of living is severely impaired.\n- The reduction period **begins with the notification of the defect** to the landlord:in and **ends with remediation**.\n- You shouldn't pay a lower rent right away, as you risk terminating because of a delay in rent. Instead, you can report to your landlady that **pay the tentative rent**. Once the defect has been rectified, you can claim back a share.\n- **how many percent** the total rent can be reduced, depends on the **severity of the defect**. You can find a clue for your case in a **rent reduction table** on the Internet.","- Ein Anspruch auf **M√§ngelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu √ºbergeben und zu erhalten.\n- Wenn w√§hrend der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende k√∂nnen f√ºr Kleinreperaturen bis etwa 100 Euro verantwortlich sein, wenn das im Mietvertrag festgelegt wurde.":"- A claim to **remedial defects** is the right to ask your **landlord to remedy the defect**.\n- Landlords have the duty to hand over and maintain the rented apartment in a usable condition.\n- If a defect occurs during the rental period, the landlord must eliminate it.\n- Exception: End of rental can be responsible for small repairs up to around 100 euros if this has been stipulated in the rental agreement.","- Ein Anspruch auf **R√ºckerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annullierung innerhalb einer Woche zur√ºckzuerhalten.\n- Zus√§tzlich erhalten Sie einen R√ºckflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der R√ºckerstattung k√∂nnen Sie auch eine anderweitige Bef√∂rderung zu Ihrem Zielort durch die Fluglinie w√§hlen.":"- A claim to **Ticket Fee Refund** is the right to receive your paid ticket price back within one week if cancelled.\n- In addition, you will receive a return flight to your first departure location if a connecting flight has been cancelled.\n- You can choose to transfer the airline to your destination instead of reimbursement.","- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** f√ºr durch den Mangel **entstandene Sch√§den erstatten** zu lassen.\n- **M√∂gliche Sch√§den** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **M√∂beln** oder auch Schmerzensgeld bei k√∂rperlichen Sch√§den.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist oder diesen bei Vertragsabschluss verschwiegen hat.":"- A claim to **damages** is the right to be reimbursed to **money** for damages incurred as a result of the defect.\n- **Possible damage** includes incurred **hotel costs**, loss of value for **furniture** that has become unusable or compensation for physical damage.\n- It is important that the lessor is responsible for the defect, is in arrears with the rectification or has not disclosed it when concluding the contract.","<0>Streitwert</0><1>Gerichtsgeb√ºhren<2/>(Vorschuss)</1><3>Anwaltshonorar<4/>(pro Seite)</3>":"<0>Amount in dispute</0> <1>Court fees<2/> (advance)</1> <3>attorney fee<4/> (per page)</3>","Abok√ºndigung":"Subscriptions","Arbeit":"Work","Aufwendungsersatz":"Reimbursement of expenses","Ausgleichszahlung":"Chargeback","Au√üergerichtliche Einigung":"Out-of-court settlement","Beratungsangebote":"Legal advice","Beseitigung des Mangels durch die Vermieter:in":"Removal of the defect by the landlords","Besteht der Schimmel aktuell noch?":"Does the mould still exist?","Betreuungsleistungen":"Care services","Datenschutz":"Privacy","Der Justizportal Wegweiser informiert Sie √ºber Ihre Rechte im Alltag und hilft Ihnen Handlungsoptionen zur L√∂sung Ihres Rechtsproblems zu finden. Probieren Sie es hier aus!":"The Solution Explorer provides you with information about your rights in everyday life and helps you to find options for action to solve your legal problem. Try it out here!","Die Kosten eines Gerichtsprozesses setzen sich aus **Gerichtsgeb√ºhren, Anwaltshonoraren und weiteren Ausgaben** zusammen.\n###  \n### Gerichtsgeb√ºhren\n- Die Gerichtsgeb√ºhren sind gesetzlich festgelegt und abh√§ngig vom Wert Ihrer Forderung (circa 10-15%).\n- Der Wert der Forderung wird als Streitwert bezeichnet und am Anfang vom Gericht festgelegt.\n- Als Kl√§ger:in m√ºssen Sie die Gerichtsgeb√ºhren im Voraus bezahlen.\n- Sollten Sie den Prozess gewinnen, bekommen Sie das Geld danach von der Gegenseite zur√ºck. Daf√ºr stellen Sie einen kurzen [Kostenfestsetzungsantrag](https://www.justiz.nrw.de/BS/formulare/zivilsachen/ziv_zwischentext/kostenfestsetzungsantrag/index.php).\n###  \n### Anwaltshonorare\n- Wenn Sie eine Anw√§lt:in vertritt, m√ºssen Sie im Voraus ein Honorar bezahlen.\n- Auch die Anwaltshonorare sind gesetzlich festgelegt, und abh√§ngig vom Streitwert (circa 20%).\n- Wer den Prozess verliert, bezahlt auch alle Anw√§lt:innen.  \n###  \n### Weitere Kosten\n- Weitere Kosten k√∂nnen entstehen, wenn zur Beweisaufnahme beispielsweise Zeug:innen geh√∂rt werden. Diese erhalten dann eine Verg√ºtung. \n- Auch Sachverst√§ndige (Expert:innen) k√∂nnen Kosten verursachen, wenn zus√§tzliche Gutachten ben√∂tigt werden.\n###  \n### Welche Kosten k√∂nnen f√ºr Sie insgesamt entstehen?\n- Die Kosten Ihres Falls k√∂nnen Sie mithilfe des Streitwerts absch√§tzen.  \n- Ihren Streitwert berechnen wir im n√§chsten Schritt, wenn Sie eine Klage erstellen.  \n- In der Tabelle finden Sie eine Orientierung, wie hoch die Kosten f√ºr verschiedene Streitwerte sein k√∂nnen.  \n- Wenn Sie eine Rechtsschutzversicherung haben, kann diese Ihre Kosten √ºbernehmen. Manchmal sind Rechtsschutzversicherungen in Mitgliedschaften inbegriffen, z.B. bei einem Mieterverein.\n- Wenn Sie nicht genug Geld f√ºr einen Gerichtsprozess haben, k√∂nnen Sie [Prozesskostenhilfe](https://www.bmjv.de/DE/Themen/GerichtsverfahrenUndStreitschlichtung/Prozesskostenhilfe/Prozesskostenhilfe_node.html) beantragen.\n###\nBeispiel: Bei Klagen zur EU-Fluggastrechteverordnung liegen Streitwerte h√§ufig unter 1.000 Euro. Damit betragen die Gerichtsgeb√ºhren circa 150 Euro. Diese erhalten Sie bei Gewinn zur√ºck. Sollten Sie verlieren, zahlen Sie die Gerichtskosten und gegebenenfalls die Honorare ihrer und der gegnerischen Anw√§lt:in (je circa 200 Euro). Das Kostenrisiko betr√§gt daher circa 300 bis 450 Euro.":"The costs of a court case consist of **court fees, attorney's fees and other expenses**.\n### \n### Court fees\n- Court fees are defined by law and depend on the value of your claim (approximately 10-15%).\n- The value of the claim is called the amount in dispute and is determined by the court at the beginning.\n- As a plaintiff, you must pay court fees in advance.\n- If you win the trial, you will then get the money back from the other party. You can do this by submitting a short [cost fixing request](https://www.justiz.nrw.de/BS/formulare/zivilsachen/ziv_zwischentext/kostenfestsetzungsantrag/index.php).\n### \n### Lawyers' fees\n- If a lawyer represents you, you must pay a fee in advance.\n- Lawyers' fees are also defined by law and depend on the amount in dispute (about 20%).\n- Anyone who loses the trial also pays all lawyers. \n### \n### Additional costs\n- Additional costs may arise if, for example, witnesses are heard in the taking of evidence. They will then receive remuneration. \n- Experts can also incur costs if additional reports are required.\n### \n### What costs can you incur overall?\n- You can estimate the costs of your case using the amount in dispute. \n- We calculate your amount in dispute in the next step when you create a claim. \n- In the table you will find an orientation as to how much the costs can be for different litigation assets. \n- If you have legal expenses insurance, it can cover your costs. Sometimes legal protection insurance is included in memberships, for example with a tenant association.\n- If you don't have enough money for a lawsuit, you can apply for [legal aid](https://www.bmjv.de/DE/Themen/GerichtsverfahrenUndStreitschlichtung/Prozesskostenhilfe/Prozesskostenhilfe_node.html).\n###\nExample: In cases of lawsuits under the EU Passenger Rights Regulation, litigation values are often below 1,000 euros. The court fees are thus around 150 euros. You will receive this back if you win. Should you lose, you will pay the court costs and, if applicable, the fees of your and the opposing lawyer (approximately 200 euros each). The cost risk is therefore around 300 to 450 euros.","Diese Frage hilft uns, Ihnen die richtigen n√§chsten Schritte zu empfehlen. Wenn der Mangel noch besteht, erkl√§ren wir Ihnen beispielsweise, wie Sie diesen beseitigen lassen k√∂nnen.":"This question helps us recommend the right next steps for you. If the defect still exists, we will explain how you can have it rectified, for example.","Diese Option ist leider noch nicht verf√ºgbar.":"Unfortunately, this option is not yet available.","Dokumentation des Mangels":"Documentation of the defect","Egal ob Sie Probleme mit Schimmel, L√§rm oder einem Wasserschaden haben - informieren Sie sich hier ganz einfach √ºber m√∂gliche Anspr√ºche und wir unterst√ºtzen Sie bei der Durchsetzung!":"Whether you have problems with mold, noise or water damage - simply find out about possible claims here and we'll help you enforce them!","Ein anderes Problem":"Another problem","Eine Klage am Amtsgericht einreichen":"File a lawsuit at the district court","Einkaufen":"Shopping","Entsch√§digungen f√ºr einen Flug":"Compensation for a flight","Erstberatung":"Initial consultation","Erstberatung bei der Verbraucherzentrale":"Initial consultation at the consumer centre","Fall Sie sich unsicher f√ºhlen, k√∂nnen Sie sich auch beraten lassen. Daf√ºr haben wir <0>hier Beratungsangebote zusammengefasst</0>.":"If you feel unsure, you can also seek advice. For this purpose, we have <0>summarized consulting services here</0>.","Familie":"Family","Flugentsch√§digungen":"Flight compensation","Fluglinie kontaktieren":"Contact airline","Gab es den Schimmel schon bei Unterzeichnung des Mietvertrags?":"Did the mold exist when signing the rental agreement?","Gebaut von <0>Tech4Germany</0>":"Built by <0>Tech4Germany</0>","Gerichtsprozesse folgen einem festen Ablauf, der im Gesetz geregelt ist. Dabei gibt es viele Sonderregelungen. Um Ihnen einen √úberblick zu geben, haben wir hier eine vereinfachte Grafik erstellt.":"Court proceedings follow a fixed sequence, which is regulated by law. There are many special regulations for this. To give you an overview, we have created a simplified graphic here.","Gerichtsverfahren":"Trials","Haben Sie Ihrer Vermieter:in eine realistische Frist gesetzt, um den Schimmel zu entfernen?":"Have you set your landlord in a realistic deadline to remove the mold?","Haben Sie den Schimmel Ihrer Vermieter:in schriftlich gemeldet?":"Have you reported your landlord's mould in writing?","Haben Sie den Schimmel bei Ihrer Vermieter:in angezeigt und angek√ºndigt, Ihre Miete nur unter Vorbehalt weiter zu zahlen (Mietminderung)?":"Have you displayed the mould to your landlord:in and announced that you would only continue to pay your rent under reservation (rent reduction)?","Haben Sie den Schimmel selbst beseitigt, und sind Ihnen dabei Kosten entstanden?":"Did you remove the mould yourself and have you incurred costs in the process?","Haben Sie den Schimmel verursacht?":"Did you cause the mold?","Heizungsausfall":"Heater failure","Ihr Flug war versp√§tet, wurde annulliert oder Ihnen wurde die Bef√∂rderung verweigert? Setzen Sie hier m√∂gliche Anspr√ºche aus der EU-Fluggastrechteverordnung durch!":"Was your flight delayed, cancelled or refused boarding? Enforce possible claims from the EU Passenger Rights Regulation here!","Ihr Vertrag wurde unerwartet verl√§ngert oder Ihnen sind unvorhergesehene Kosten entstanden? In Zukunft unterst√ºtzen wir Sie hier in rechtlichen Problemen bei Abofallen.":"Has your contract been extended unexpectedly or have you incurred unforeseen costs? In the future, we will support you in dealing with legal problems with subscription cases.","Impressum":"Imprint","Info":"Info","Ist die Frist zur M√§ngelbeseitigung schon abgelaufen?":"Has the deadline for rectification of defects already expired?","Ja":"Yes","Jetzt starten!":"Start now!","Klage einreichen":"File a lawsuit","K√ºndigung":"Termination","Landesspezifische Schlichtungsstellen":"Country-specific arbitration bodies","L√§rm":"Noise","Mehr erfahren":"Learn more","Mieterh√∂hung":"Rent increase","Mietervereine / Deutscher Mieterbund":"Tenant Clubs/German Tenant Association","Mietminderung":"Rent reduction","Mietm√§ngel":"Rental defects","Mietpreisbremse":"Rent brake\n(Mietpreisbremse)","M√§ngel in der Wohnung":"Deficiencies in the apartment","M√§ngelanzeige bei Ihrer Vermieter:in":"Notice letter of defects to your landlord","M√∂gliche Anspr√ºche":"Possible claims","M√∂gliche L√∂sungen":"Possible solutions","Nebenkostenabrechnung":"Billing of incidentals","Nein":"No","Nur kostenlose Angebote anzeigen":"Show free offers only","N√§chste Schritte":"Next steps","Online Klage einreichen":"File a lawsuit online","Optionen":"Options","Problem einordnen":"Classify issue","Rechtlich beraten lassen":"Get legal advice","Rechtsantragsstelle am Amtsgericht":"Legal application office at the district court","Rechtsberatung bei Anw√§lt:innen":"Legal advice by lawyers","Reisen & Verkehr":"Travel & traffic","Renovierung bei Auszug":"Renovation when moving out","R√ºckerstattung der Flugscheinkosten":"Ticket reimbursement","Schadensersatz":"Damages","Schimmel":"Mould","Schimmel in der Wohnung kann durch Baufehler entstehen, f√ºr die Sie nicht verantwortlich sind. Allerdings kann auch seltenes L√ºften und Heizen oder das Trocknen von W√§sche zu Schimmel f√ºhren.\nWenn Sie durch Ihr Verhalten den Schimmel ganz oder teilweise mitverursacht haben, bestehen in der Regel keine Anspr√ºche gegen ihre Vermieter:in.":"Mold in the apartment can be caused by construction errors for which you are not responsible. However, rare airing and heating or drying laundry can also lead to mold.\nIf your behaviour has caused the mold in whole or in part, there are usually no claims against your landlord.","Schlichtungsantrag stellen":"Submit a request for conciliation","Sie haben Ihren Mangel angezeigt und eine Frist zur Beseitigung gesetzt? Dann m√ºssen Sie zun√§chst abwarten, bis diese abgelaufen ist. Falls der Mangel danach noch nicht beseitigt wurde, k√∂nnen Sie weitere Schritte unternehmen und zum Beispiel den Mangel selbst beseitigen.":"Have you reported your defect and set a deadline for eliminating it? Then you must wait until it expires. If the defect has not been rectified afterwards, you can take further steps, for example, to correct the defect yourself.","Sie haben sich bereits √ºber Ihre Anspr√ºche informiert und Ihre Handlungsoptionen ausgesch√∂pft? Dann unterst√ºtzen wir Sie auch direkt bei der Erstellung und Einreichung einer Klage f√ºr <0>Mietm√§ngel</0> oder <1>Fluggastrechte</1>. Falls nicht, empfehlen wir zun√§chst unseren Justizportal Wegweiser:":"Have you already found out about your requirements and exhausted your options for action? Then we can also support you directly in drafting and filing a claim for <0>rental defects</0> or <1>air passenger rights</1>. If not, we recommend our Justice Portal guide first:","Sie k√∂nnen eine Klage **direkt hier √ºber das Justizportal einreichen**. Wenn Sie sich unsicher f√ºhlen, kann Ihnen auch eine Anw√§lt:in dabei helfen.\nWenn alle Dokumente vorhanden sind, sollte das Ausf√ºllen der Klage **etwa 20 Minuten** dauern.":"You can **file a claim directly here via the justice portal**. If you feel unsure, a lawyer can also help you with this.\nOnce all the documents are in place, the lawsuit should **take about 20 minutes** to complete.","Startseite":"Homepage","Stromausfall":"Blackout","St√§dtische Mietberatungen":"Municipal rental advice","Unser Angebot":"Our offer","Verkehrsunfall":"Traffic accident","Vermieter:in erneut kontaktieren":"Contact Landlord in again","Warum?":"Why?","Wasserschaden":"Water damage","Wegweiser":"Solution Explorer","Weitere M√∂glichkeiten":"More options","Welche Kosten entstehen in einem Gerichtsprozess?":"What are the costs of a court case?","Wenn Ihnen durch den Mangel keine Kosten oder Sch√§den entstanden sind, Sie keine Mietminderung angek√ºndigt haben und der Mangel bereits behoben wurde, haben Sie vermutlich keine weiteren rechtlichen Anspr√ºche.":"If you did not incur any costs or damage due to the defect, you have not announced a reduction in rent, and the defect has already been resolved, you probably have no further legal claims.","Wenn Ihre Vermieter:in auch nach Aufforderung und Ablauf einer realistischen Frist den Mangel nicht beseitigt, oder es sich um eine Not- oder Eilma√ünahme handelt, k√∂nnen Sie den Mangel auch selbst beseitigen und sich die Kosten daf√ºr erstatten lassen.":"If your landlord does not remedy the defect, even after being requested and after a realistic deadline, or if it is an emergency or urgent measure, you can also rectify the defect yourself and have the costs reimbursed for it.","Wenn Sie Ihrer Vermieter:in eine realistische Frist gesetzt haben, m√ºssen Sie ihr zun√§chst bis dahin Zeit f√ºr die Beseitigung geben. Danach haben Sie zus√§tzliche rechtliche Anspr√ºche.":"If you have set a realistic deadline for your landlord, you must first give her time for removal by then. After that, you have additional legal rights.","Wenn Sie angek√ºndigt haben, Ihre Miete nur unter Vorbehalt zu zahlen, kann Ihnen von der Ank√ºndigung bis zur Beseitigung des Mangels sein Anspruch auf Mietminderung zustehen.":"If you have announced that you are only paying your rent on a conditional basis, you may be entitled to a reduction in rent from the announcement until the defect is rectified.","Wenn Sie den Mangel schon gemeldet haben, ist entscheidend, ob Sie dabei eine realistische Frist zur Beseitigung gesetzt haben. In der Regel sollten 2-3 Wochen ausreichen, um den Mangel zu beheben oder Ihnen zumindest einen Termin f√ºr eine Reperatur zu nennen. \nWenn Sie keine oder eine zu kurze Frist gesetzt haben, helfen wir Ihnen, ein neues Schreiben aufzusetzen.":"If you have already reported the defect, it is crucial whether you have set a realistic deadline for remedying it. Usually 2-3 weeks should be enough to fix the defect or at least give you an appointment for a repair. \nIf you have not set a deadline, or if you have set a deadline too short, we can help you rewrite a letter.","Wenn Sie den Mangel selbst verursacht haben, k√∂nnen vermutlich keine rechtlichen Anspr√ºche durchgesetzt werden. Eine Ausnahme ist, wenn Versicherungen f√ºr den Eintritt eines Mangels bestehen. Zahlen Sie √ºber Ihre Betriebskosten zum Beispiel eine Geb√§udeversicherung mit, dann muss diese bei Sch√§den zahlen. \nIn jedem Fall sollten Sie Ihre Vermieter:in kontaktieren, damit der Mangel beseitigt werden kann. [Nutzen Sie daf√ºr die Vorlage unserer M√§ngelanzeige](/formular?mangelanzeige&mangelart=schimmel).":"If you caused the defect yourself, it is likely that no legal claims can be enforced. An exception is if there is insurance for the occurrence of a defect. If you, for example, pay for building insurance via your operating costs, then you have to pay for damage. \nIn any case, you should contact your landlord so that the defect can be rectified. [Use the submission of our defect report](/formular?mangelanzeige&mangelart=schimmel).","Wenn Sie einen Mangel schon bei Unterschrift des Mietvertrags kannten, aber dabei nicht um Verbesserung gebeten haben, k√∂nnen Anspr√ºche ausgeschlossen sein. \nAnders ist die Situation, wenn der Mangel erst sp√§ter entstanden ist, oder bei Vetragsabschluss verschwiegen oder sogar versteckt wurde.":"If you knew about a defect when you signed the lease but did not ask for improvement, claims may be excluded. \nThe situation is different if the defect arose later, or was concealed or even hidden when the contract was concluded.","Wenn der Mangel noch besteht, sollten Sie diesen zuerst Ihrer Vermieter:in melden, um ihn beseitigen zu lassen. Sie sollten dabei eine schriftliche M√§ngelanzeige verschicken, um einen Nachweis zu haben.\nWenn Sie das noch nicht getan haben, helfen wir Ihnen dabei.":"If the defect still persists, you should report it to your landlord first to have it rectified. You should send a written notice of defects in order to have proof.\nIf you haven't done that yet, we'll help you do it.","Wenn der Mangel schon bei Vertragsschluss bekannt war, k√∂nnen vermutlich keine rechtlichen Anspr√ºche durchgesetzt werden. Sie k√∂nnen aber den direkten Austausch mit Ihrer Vermieter:in suchen.":"If the defect was already known when the contract was concluded, no legal claims can probably be enforced. However, you can seek direct exchange with your landlord.","Wenn durch den Mangel nicht nur an der Wohnung, sondern auch an Ihrem Eigentum Sch√§den entstanden sind, haben sie vielleicht Anspr√ºche auf Schadensersatz.\nZus√§tzliche Sch√§den k√∂nnen sein, wenn durch den Mangel zum Beispiel eigene M√∂bel oder Kleidung besch√§digt wurden.":"If the defect caused damage not only to the apartment, but also to your property, you may be entitled to compensation.\nAdditional damage can occur if the defect has damaged one's own furniture or clothing, for example.","Wie es funktioniert":"How it works","Wie l√§uft ein Gerichtsprozess ab?":"How does a court case work?","Wobei haben Sie ein Problem?":"What do you have a problem with?","Wohnen":"Dwelling","Wurde durch den Schimmel Eigentum von Ihnen besch√§digt?":"Has the mold damaged your property?","Zum Prototypen":"Go to the prototype","Zum Wegweiser":"Solution Explorer","Zur Erstellung des Klagedokuments":"For the preparation of the claim document","Zur Klageerstellung":"To the filing of a claim","Zur Vorlage f√ºr die M√§ngelanzeige":"To the template for the defect report","Zur √úbersicht f√ºr Beratungsangebote":"Go to overview for advisory services","Zur√ºck":"Back","Zur√ºck zur Homepage":"Back to homepage","back":"Back","banner":"This page is a prototype of the <0>Tech4Germany Fellowship 2021</0> and does not provide binding legal information.","co.info_evv":"**Offer**: \n- Examination of legal claims by lawyers of the German Tenants Association in a conversation.\n- Proposal of initial courses of action.\n######\n**Availability**: \n- telephone or personal advice, throughout Germany. [You can find the website for your state here](https://www.verbraucherzentrale.de/beratung).\n######\n**Cost**: \n- About 20-30 euros for a twenty-minute consultation.\n######\nThe consumer centres of the federal states, together with the German Tenant Association, offer inexpensive initial advice by lawyers, [in Baden-W√ºrttemberg, for example, from 20 euros](https://www.verbraucherzentrale-bawue.de/beratungsstellen/746/kontakt/Mietrecht/17938).","co.info_ls":"**Offer**: \n- Mediation between you and the other party for an agreement by a trained, independent arbitrator.\n######\n**Availability**: \n- In some federal states, for example [Berlin](https://www.berlin.de/sen/verbraucherschutz/verbraucherportal/recht/vertragsrecht/schlichtung-261337.php) and [North Rhine-Westphalia](https://streitschlichtung.nrw.de/JOLStreit/).\n######\n**Cost**: \n- For example, between 10 and 40 euros in North Rhine-Westphalia.\n######\nSome federal states offer conciliation bodies that help citizens settle conflicts amicable. An arbitrator mediates between both parties. These positions are particularly helpful if you want to maintain the relationship with the other party - for example in a tenancy relationship.","co.info_mm":"**Offer**: \n- Advice and out-of-court representation on all rental issues by permanent lawyers.\n- On request, communication with your landlord can be taken over.\n- Some offers in third languages.\n######\n**Availability**: \n- In many German cities. [You can find the closest regional association of the German Tenants Association for you here](https://www.mieterbund.de/beratung/mieterverein-vor-ort.html).\n######\n**Cost**: \n- Some free initial consultation.\n- Annual memberships for around 100 euros, minimum term of two years.\n######\nThere are hundreds of regional tenant associations in Germany where lawyers offer advice to members. If there is no club in your city, membership is often also possible from other cities.","co.info_ra":"**Offer**: \n- Legal advice as well as out-of-court and judicial representation on all legal issues.\n######\n**Availability**: \n- Throughout Germany.\n- [Via the Lawyers' Information](https://anwaltauskunft.de/magazin), a portal of the German Bar Association, you can find a lawyer near you.\n######\n**costs**: \n- An initial consultation costs a maximum of 190 euros, but can also be significantly cheaper.\n- Free advice is possible with legal protection insurance or a consulting assistance certificate for financially weak persons.\n- Here you can find a [request for advice in German](https://justiz.de/service/formular/dateien/agI1.pdf) with additional [notes in English](https://justiz.de/service/formular/dateien/agl1-hinweisblatt_beratungshilfe_englisch.pdf).\n######\nLawyers can represent you in legal matters to the other side and in court. Every citizen has the right to be advised and represented by a lawyer on legal issues.","co.info_ram":"**Offer**: \n- Help with filing lawsuits and applications.\n- answering questions about letters from the court.\n######\n**Availability**: \n- There are local courts in every medium-sized German city.\n- You can contact the legal application office there in person on site or by phone.\n- [You can find the nearest district court for your place of residence here](https://www.justizadressen.nrw.de/de/justiz/suche).\n######\n**Cost**: \n- It's free.\n######\nIf you need help filing a claim or answering a court letter, the legal application office at the district court can help you. You can arrange a free appointment with a trained legal guarder for this purpose. This ensures that your statements receive all legally required information. However, individual legal advice is not possible.","co.info_sm":"**Offer**:\n- First point of contact for urgent rental questions (e.g. rent increase, termination), some advice in foreign languages.\n######\n**Availability**: \n- Telephone or personal advice in many major cities, for example in [Berlin](https://www.stadtentwicklung.berlin.de/wohnen/mieterberatungen.shtml), [Munich](http://www.muenchen.de/dienstleistungsfinder/muenchen/1074551/), [Hamburg](https://www.hamburg.de/oera/), [Stuttgart]( https://www.stuttgart.de/vv/verwaltungseinheit/beratungstelefon-fuer-mieter-und-vemieter.php) or [Nuremberg](https://www.nuernberg.de/internet/wohnen/mietrechtsinformationen.html).\n###### \n**Cost**:\n- Most offers are available free of charge.\n######\nSome cities offer free tenant advice. These counselling centres are often intended for urgent problems (immediate dismissal, eviction action) or citizens with limited financial resources.","consultation.header":"Legal advice","consultation.sub_header":"Since the justice portal cannot always inform you, we have provided an overview of cost-effective advice centres for you here.","exit.footer":"Are you looking for more support? Then you can contact legal advice centres that you can\nbe able to inform you individually about your case. In a consultation, experts assess whether legal\nDemands exist and how you can best enforce them. For that, we have [here's an overview of\nprepared counselling centres](/beratungsangebote).","exit.heading":"Unfortunately, we didn't cover your case.","forward":"Continue","home.header":"Judiciary on the net.<0/> Simple, online, anytime.","home.howitwork_1_heading":"Classify problem","home.howitwork_1_subheading":"By answering simple yes-no questions, we help you find relevant and free legal information.","home.howitwork_2_heading":"Inform about claims","home.howitwork_2_subheading":"Based on your information, we provide you with appropriate information about possible legal claims.","home.howitwork_3_heading":"Compare options","home.howitwork_3_subheading":"From advice services to practical tips and templates to a lawsuit in court, you'll find the right way.","home.howitwork_heading":"How it works","home.howitwork_lawsuit":"When all other options are exhausted, we'll help you file a claim with the district court online. To this end, we explain the process of our online process to you and provide transparent information on the costs of legal proceedings as well as possible legal advice.","home.howitwork_subheading":"We can help you solve your legal problem in three easy steps:","home.sub_heading":"A digital service offering from the German judiciary. We support you with legal problems in everyday life.","nextsteps.header":"End of Solution Explorer: Possible next steps","nextsteps.sub_header":"We've summarized next steps to enforce your claims here.<0/> Click the boxes one at a time to learn more.","ns.info_2contact_landlord":"**Why?** \n- A second letter can make your claim more strong.\n- If you haven't received a response from your landlord, she may have missed your notice of defects.\n- If new costs have been incurred (e.g. expenses due to your own rectification of defects), you can claim them directly from your landlord. \n### \n**How?** \n- Contact your landlord and point them back to the problem. \n- Clearly state your claim and set a final deadline for rectification or payment before taking further legal action.\n- Keep it friendly and polite.","ns.info_ca":"**Why?** \n- If you contact your airline with a correct claim, they usually pay for your claim directly. \n- With a written request, you also have proof of the further process. \n### \n**How?** \n- Use your airline's EU air passenger rights contact form and make your claim. \n- Contact forms can be found on the websites of all major airlines, such as [Ryanair](https://onlineform.ryanair.com/de/de/eu-261), [Lufthansa](https://www.lufthansa.com/de/de/feedback), \n[British Airways](https://www.britishairways.com/travel/customerportal/public/de_gb#/customer-service-portal), [AirFrance-KLM](https://wwws.airfrance.de/claim), or [easyJet](https://www.easyjet.com/de/claim/EU261).\n- If you can't find a form, you can also submit your claim by mail or email. Describe your case specifically and set a deadline for payment, for example, two to three weeks. \n- You can take further steps after the deadline.","ns.info_consultation":"**Why?** \n- In legal advice, your case can be assessed individually by an expert. \n- They can also contact your landlord again. \n- Sometimes, for example, a letter from a lawyer can help convince the landlord of your claims. \n### \n**How?** \n- There are local counselling services for tenants in many cities. \n- Some advice centres are available free of charge, especially for initial consultation or financially weak individuals. \nOn the next page, we will give you an overview of advice centers.","ns.info_contact_landlord":"**Why?** \n- As a tenant, you are obliged to report defects in your apartment.\n- Landlords usually eliminate a defect as soon as they are notified. \n- With a written notice of defects, you also have proof of the further process. \n### \n**How?** \n- Contact your landlor:in and describe the defect objectively. \n- Clearly state your claim and set your landlord a realistic deadline to remedy the defect. \n- Propose to pay rent only under reservation if a claim for rent reduction appears possible for you.\n- Keep it friendly and polite. \n### \nWe can help you create a defect report here:","ns.info_docu":"**Why?** \n- To have proof of your landlord and the further process, you should document the defect. \n### \n**How?** \n- If the defect is visible, such as a mold stain or water damage, you can take photos or videos of all affected areas of the home. \n- If the defect is not visible, for example in case of noise disturbance, you can write down when and in what form it occurs. A video or testimonies can also help.","ns.info_file_claim":"**Why?** \n- A lawsuit is the last way to enforce your claims if the other party doesn't voluntarily meet them. \n- With a lawsuit, you are requesting that a judge should: rule your case in court. \n### \n**How?** \n- First, you can check with us about the process and the costs of a court case.\n- Then we will help you create and file a statement of claim.\n- In court, the judge tries to find a solution for you and the other side.\n- If you cannot agree, the judge will decide your case with a verdict. \n### \nIt is important that you have already prompted the **other side directly** to meet your requirements.\nHere we will inform you about the process of a lawsuit and help you file it.","ns.info_s":"**Why?** \n- If you've already contacted your airline and they haven't complied with your request, arbitration can help.\n- Arbitration does not incur any costs, but the decision is not binding on the airline.\n### \n**How?** \n- Submit a request for conciliation two months after you first contacted the airline.\n- For [most European airlines](https://soep-online.de/assets/files/20.07.soep-Mitgliederliste.pdf), you can submit a [application to the SOEP (Conciliation Board for Public Passenger Transport e.V.)](https://soep-online.de/ihre-beschwerde/). Otherwise, the [Air Transport Arbitration Board at the Federal Office of Justice](https://www.bundesjustizamt.de/DE/Themen/Buergerdienste/Luftverkehr/Schlichtungsstelle_node.html) is responsible for your case.\n- If your request is admissible and eligible, the body will recommend arbitration to you and the airline.\n- The entire conciliation process takes about three months. After that, you can still file a lawsuit.","pe.header":"Possible legal claims","pe.sub_header":"The following claims could result from your information. A claim is the right to demand something from another person or company. You can also enforce multiple claims at the same time. \nClick on the claims to learn more about them. We will then inform you about possible next steps on the next page.","pe.to_next_steps":"To the next steps","zpo.heading":"File a lawsuit at the district court","zpo.nextsteps":"1. On the next page, you can create the document for a lawsuit related to your case. \n2. We will estimate the legal costs for you and inform you about the next steps you need to take. \n3. Once created, you can review all information first. **Only then do you decide on the submission.**"}};